button {
    cursor: pointer;
    padding: .5rem 1rem;
    border: 2px solid #ffffff;
    background-color: #f48847;
    color: white;
    border-radius: 12px;
    margin-right:5px;
}

.bigButton {
  padding: 1rem 2rem;
}

.semiBigButton {
  padding: .75rem 2rem;
}

button:hover,
button:active {
  background-color: #724021;
  border-color: #724021;;
}

button.alternative {
  color: #724021; 
  border-color: transparent;
  background-color: transparent;
}

button.alternative:hover,
button.alternative:active {
  background-color: #724021;
}
